.login-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login-wrapper input {
  border: 2px solid white;
}

.login-wrapper label {
  color: #575757;
  font-size: 20px;
  font-weight: bold;
}

.login-header {
  margin-top: 3vw;
  margin-bottom: 5vw;
  color: #088f30;
}

.login-header img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 30%;
}

.login-header h1 {
  font-size: 35px;
  text-align: center;
}

.login-header hr {
  display: block;
  height: 1vw;
  border: 0;
  border-top: 0.2vw solid #9b9b9b;
  margin: 1vw 0;
  padding: 0;
}

form {
  margin: 30px;
}

footer {
  position: fixed;
  bottom: 0px;
  left: 0px;
  right: 0px;
  margin-bottom: 0px;
}

@media screen and (max-width: 950px) {
  footer {
    position: relative;
    margin-top: 5vw;
  }
}
